.app {
  text-align: center;
}

.container {
  position: relative;
  width: auto;
  max-width: 940px;
  margin: 0 auto;
  padding: 0 10px;
}

.container.logo {
  height: 70px;
}

.header {
  padding-top: 10px;
  position: relative;
  z-index: 99;
}

.header .leftLogo {
  float: left;
  clear: left; 
  padding-left: 20px;
}

.header .leftLogo  img {
  height: 100px;
  width: 130px;
}

.header .rightLogo {
  float: right;
  clear: right;
  padding: 20px;
}

.header .rightLogo  img {
  height: 50px;
  width: 317px;
}

.nav-wrap {
  margin-top: 40px;
}

.nav-wrap a {
  text-decoration: none;
  color: #fff;
  display: block;
}

.nav-wrap ul {
  list-style: none;
  position: relative;
  padding: 0;
}

.nav-wrap li {
  float: left;
}

.nav-wrap li.primary-list {
  width: 20%;
  background: url('./assets/img/menu-separator.png') no-repeat scroll 0 center;
  text-transform: uppercase;
  font-size: 14px;
}
.nav-wrap ul.primary li.primary-list:hover a {
  color: #b92827;
}
.nav-wrap li.primary-list.-first{
  width: 20%;
  background: none;
}

/* clear'n floats */
.nav-wrap ul:after {
  clear: both;
}

.nav-wrap ul:before,
.nav-wrap ul:after {
    content: " ";
    display: table;
}

.nav-wrap nav {
  position: relative;
  background: #b92827;
  text-align: center;
}

/* prime */
.nav-wrap ul.primary li a {
  display: block;
  padding: 12px 30px;
  font-weight: 500;
}

.nav-wrap ul.primary li:last-child a {
  border-right: none;
}

.nav-wrap ul.primary li a:hover {
  color: #000;
}

/* subs */
.nav-wrap ul.sub {
  position: absolute;
  z-index: 200;
  -webkit-box-shadow: 2px 2px 0 #BEBEBE;
          box-shadow: 2px 2px 0 #BEBEBE;
  width: 25%;
  display:none;
}

.nav-wrap ul.sub li {
  float: none;
  margin: 0;
  text-align: left;
}

.nav-wrap ul.primary li.primary-list ul.sub li a {
  border-bottom: 1px dotted #ccc;
  border-right: none;
  color: #b92827;
  padding: 15px 15px;
  font-size: 13px;
  text-transform: none;
}

.nav-wrap ul.primary li.primary-list ul.sub li:hover a {
  color: #fff;
  background-color: #b92827;
}

.nav-wrap ul.sub li:last-child a {
  border-bottom: none;
}

.nav-wrap ul.sub li a:hover {
  color: #000;
  background: #eeeeee;
}

/* sub display*/
.nav-wrap ul.primary li:hover ul {
  display: block;
  background: #fff;
}

/* keeps the tab background white */
.nav-wrap ul.primary li:hover a {
  background: #fff;
  color: #666;
  text-shadow: none;
}

.nav-wrap ul.primary li:hover > a{
  color: #000;
} 

@media only screen and (max-width: 600px) {
  .nav-wrap {
    width: 100%;
    margin-top: 0px;
  }
  
  .nav-wrap li {
    float: none;
  }
  
  .nav-wrap ul.primary li:hover a {
    background: none;
    color: #8B8B8B;
    text-shadow: 1px 1px #000;
  }

  .nav-wrap ul.primary li:hover ul {
    display: block;
    background: #272727;
    color: #fff;
  }
  
  .nav-wrap ul.sub {
    display: block;  
    position: static;
    -webkit-box-shadow: none;
            box-shadow: none;
    width: 100%;
  }
  
  .nav-wrap ul.sub li a {
    background: #272727;
    border: none;
    color: #8B8B8B;
  }
  
  .nav-wrap ul.sub li a:hover {
    color: #ccc;
    background: none;
  }
}


